<template>
	<section>

		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getSpaces"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="timetasks" highlight-current-row
							v-loading="listLoading"
							@selection-change="selsChange"
							style="width: 100%;">

			<el-table-column type="selection" width="40">
			</el-table-column>
			<el-table-column type="index">
			</el-table-column>
			<el-table-column prop="name" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="expression" label="" width="140" sortable>
			</el-table-column>
			<el-table-column prop="last" label="">
			</el-table-column>
      <el-table-column prop="next" label="">
      </el-table-column>
			<el-table-column label="">
				<template scope="scope">
          <div v-if="!testStatus(scope.row.status)">
            <el-button type="warning" size="small"></el-button>
            <el-button type="default" size="small"></el-button>
            <el-button type="primary" size="small"></el-button>
          </div>
          <div v-else>
            <el-button type="success" size="small"></el-button>
            <el-button type="default" size="small"></el-button>
            <el-button type="primary" size="small"></el-button>
          </div>
				</template>
			</el-table-column>

		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.status"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.description"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="status">
					<el-input-number v-model="addForm.status"></el-input-number>
				</el-form-item>
				<el-form-item label="" prop="description">
					<el-input v-model="addForm.description" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>

	export default {
		data() {
			return {
				// 
				filters: {
					name: ''
				},

				spaces: [],
				errors: [],
				total: 0,
				page: 1,
				sels: [],  // 
				listLoading: false,

				editFormVisible: false, // 
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},

				//
				editForm: {
					id: 0,
					name: '',
					status: 1,
				  description: ''
				},

				addFormVisible: false, // 
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},

				// 
				addForm: {
					id: null,
					name: '',
					status: 1,
				  description: ''
				},

        timetasks: [
          {"id": 1, "name":"", "expression": "* 0 0 * * *", "last": "", "next": "2017-05-27 02:00:00", "status" : 1},
          {"id": 2, "name":"", "expression": "@monthly", "last": "2017-04-27 02:00:00", "next": "2017-05-27 02:00:00", "status" : 0},
          {"id": 3, "name":"", "expression": "@weekly", "last": "2017-05-27 02:00:00", "next": "2017-05-28 02:00:00", "status" : 1},
        ]

			}
		},

		methods: {
			handleCurrentChange(val) {
				this.page = val;
				this.getSpaces();
			},

      testStatus(status) {
        if(status === 1) {
          return true;
        }
        return false;
      },

			formatStatus(row, column) {
				if(row.status === 0) {
					return '';
				} else if(row.status === 1) {
					return '';
				} else {
					return '';
				}
			},

			// 
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.removeSpaceById(row.id);
				}).catch(() => {

				});
			},

			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},

			// 
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					id: 0,
					name: '',
					status: 1,
				  description: ''
				};
			},

			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							let para = JSON.stringify(Object.assign({}, this.editForm));
							// console.log(JSON.stringify(para));
							this.updateSpaceById(para);
						});
					}
				});
			},

			// 
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							let para = Object.assign({}, this.addForm);
							this.addNewSpace(JSON.stringify(para));
						});
					}
				});
			},

			selsChange: function (sels) {
				this.sels = sels;
			},

			// 
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					// this.listLoading = true;
					//
					// // NProgress.start();
					// let para = { ids: ids };
					// batchRemoveUser(para).then((res) => {
					// 	this.listLoading = false;
					// 	// NProgress.done();
					// 	this.$message({
					// 		message: '',
					// 		type: 'success'
					// 	});
					// 	this.getSpaces();
					// });
				}).catch(() => {

				});
			},

			// 
	    getSpaces () {
	      let url = 'http://m.qmen.space:8080/api/space';

	      this.$http.get(url).then(response => {
	        this.spaces = response.data
	      }).catch(e => {
	        this.errors.push(e)
	      })
	    },

	    // id
	    getSpaceById (id) {
	      let url = 'http://m.qmen.space:8080/api/space/' + id

	      this.$http.get(url).then((response) => {
	        this.spaces = response.data;
	        // console.log(response.data)
	      }).catch((error) => {
	        console.log(error)
	      })
	    },

	    // id
	    removeSpaceById (id) {
	      let url = 'http://m.qmen.space:8080/api/space/' + id

				this.listLoading = true;
	      this.$http.delete(url).then((response) => {
					this.listLoading = false;
					this.$message({
						message: '',
						type: 'success'
					});
					this.getSpaces();
	      }).catch((error) => {
	        console.log(error)
	      })
	    },

	    // 
	    addNewSpace (para) {
	      let url = 'http://m.qmen.space:8080/api/space'
	      this.$http.post(url, para).then(response => {
					this.addLoading = false;
					// NProgress.done();
					this.$message({
						message: '',
						type: 'success'
					});
					// this.$refs['addForm'].resetFields();
					this.addFormVisible = false;
					this.getSpaces();
	      }).catch(e => {
	        this.errors.push(e)
	      })
	    },

	    // id
	    updateSpaceById (para) {
	      let url = 'http://m.qmen.space:8080/api/space'
				// this.listLoading = false;
	      this.$http.put(url, para)
	      .then(response => {
					this.$message({
						message: '',
						type: 'success'
					});
					// this.$refs['editForm'].resetFields();
					this.editLoading = false;
					this.editFormVisible = false;
					this.getSpaces();
	      }).catch(e => {
					this.errors.push(e)
					this.$message({
						message: '' + this.errors,
						type: 'error'
					});
					// this.$refs['editForm'].resetFields();
					this.editFormVisible = false;
					this.editLoading = false;
	      })
	    }
		},

		mounted () {
			this.getSpaces();
		}
	}
</script>

<style scoped>

</style>



